/* The animation code */
@keyframes example {
    from {
      opacity: 1;
    }
    to {
      opacity: 0;
    }
  }
  
  #fadeOutText {
    opacity: 0;
    background-color: rgb(142, 245, 239);
    position: absolute;
    top: 0;
    width: 100%;
    /* text-align: center; */
    height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    animation-name: example;
    animation-duration: 4s;
  }
  
  /* The element to apply the animation to */
  #scrollingText {
    display: 'flex';
    position: 'absolute';
    top: 0;
    opacity: 0.7;
    text-align: 'center';
    width: '100%';
    justify-content: 'center';
    flex-direction: 'column';
  }
